    .field__bg .field__block + .icon-24 {
        margin-right: 23px;
    }
    .field__bg.search .field__block::before {
        left: 23px;
        top: 23px;
    }
    .field {
        font-size: 20px;
        line-height: 32px;
        padding: 32px 23px 6px;
    }
    .field.field-duo {
        padding: 32px 23px 6px 23px;
    }
    .field:focus + .field__label {
        font-size: 16px;
        line-height: 24px;
        top: 9px;
    }
    textarea.field {
        padding-top: 32px;
    }
    .field__label {
        font-size: 20px;
        line-height: 32px;
        left: 23px;
        right: 23px;
        top: 16px;
    }
    .field__filled .field__label {
        font-size: 16px;
        line-height: 24px;
        top: 9px;
    }
    .field__error_message {
        margin-top: 20px;
        padding-left: 24px;
        padding-right: 24px;
    }
    .icon-box + .icon-box {
        margin-left: 16px;
    }
    .field__block [class^="bg-icon-24"] {
        right: 17px;
        top: 23px;
    }
    p + .docs-table {
        margin-top: 40px;
    }
    .messages__container {
        border-radius: 16px;
        font-size: 16px;
        line-height: 24px;
        top: 16px;
    }
    .messages__container > .inner-content {
        padding-left: 63px;
        padding-right: 63px;
    }
}
@media screen and (min-width: 1224px) {
    [id="menu"] {
        display: none;
    }
}
@media screen and (max-device-width: 480px) {
    body {
        -webkit-text-size-adjust: none;
    }
}
@-moz-keyframes preloader {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@-webkit-keyframes preloader {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@-o-keyframes preloader {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@keyframes preloader {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

/* error page */
.error-page__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    box-sizing: border-box;
    padding: 72px 24px 92px 24px;
}
.error-page__center {
    width: 100%;
    max-width: 432px;
}
.error-page__icon {
    display: inline-block;
    width: 80px;
    height: 80px;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("data:image/svg+xml;base64,");
}
.error-page__header {
    font-size: 24px;
    line-height: 36px;
    margin-top: 24px;
}
.error-page__text {
    font-size: 17px;
    line-height: 24px;
    margin-top: 16px;
    color: #77767a;
}
.error-page__button-container {
    margin-top: 16px;
}
.error-page__button {
    display: block;
    padding: 24px;
    color: #7633FF;
    font-size: 20px;
    line-height: 32px;
    margin-left: -24px;
}
.error-page__button:visited {
    color: #7633FF;
}
.error-page__button:hover {
    background: rgba(118, 51, 255, 0.08);
}

@media screen and (min-width: 720px) {
    .error-page__center {
        max-width: 504px;
    }
}
@media screen and (min-width: 1920px) {
    .error-page__header {
        font-size: 32px;
        line-height: 48px;
    }
    .error-page__text {
        font-size: 20px;
        line-height: 32px;
    }
    .error-page__button-container {
        margin-top: 24px;
    }
    .error-page__button {
        padding: 32px;
        margin-left: -32px;
    }
}
