/* swipe animation : start */
.swipe_getout {
    animation-name: swipe_getout;
    animation-duration: 0.8s;
    animation-delay: 0.1s;
    animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
    animation-iteration-count: 1;
}

@-moz-keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

@-webkit-keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

@-o-keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

@keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

