    margin-top: 64px;
}
.docs-list {
    counter-reset: item;
}
.docs-list li + li {
    margin-top: 48px;
}
.docs-list li::before {
    color: #7633ff;
    counter-increment: item;
    content: counter(item) '. ';
    display: block;
    font-size: 24px;
    line-height: 1.5;
    margin-left: 24px;
}
.docs-list li .title {
    font-size: 24px;
    line-height: 1.5;
}

@media screen and (min-width: 1920px) {
    .docs-list li + li {
        margin-top: 64px;
    }
    .docs-list li::before {
        font-size: 32px;
        margin-left: 32px;
    }
    .docs-list li .title {
        font-size: 32px;
    }
}
